.nav {
  &-account {
    display: flex;

    &__item {
      font-family: $ff-text;
      font-size: 1.8rem;
      line-height: 1.2;
      letter-spacing: 0.375rem;
      margin-left: 5rem; 

      display: flex;
      align-items: center;
       
    }

    &__link {
      color: $text-color;
      transition: all .3s ease;
    }
  }
}
