.vacancies {
  margin-top: 8.3rem;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 18px;
  color: $tcolor;

  &__list {
    padding: 2.8rem 10rem 2.8rem 2rem;
    border: 1px solid rgba(84, 70, 143, 0.1);
    background-color: #efefef;
    display: none;

    &:first-child {
        display: block;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 1.13px;
    line-height: 21px;
    text-transform: uppercase;
    margin-bottom: 2rem;
  }
}

.vacancy {
  padding: 2.8rem 0 1.8rem;
  border-top: 1px solid rgba(84, 70, 143, 0.1);

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    .like {
      font-size: 2.2rem;
      color: #b82532;
    }

    .dislike {
      margin-left: 1.9rem;
      font-size: 2.2rem;
      color: #54468f;
    }
  }

  &__title {
    font-size: 16px;
    font-weight: 900;
    letter-spacing: 1px;
    line-height: 19px;
    margin-bottom: 2.4rem;
  }

  &__text {
    margin-bottom: 2rem;
    cursor: pointer;
  }

  &__skills {
    display: flex;
    flex-wrap: wrap;
  }

  &__skill {
    margin-right: 2rem;
    margin-bottom: 1rem;
    min-width: 10rem;
    padding: 1rem;
    border-radius: 1rem 0 1rem 0;
    background-color: #54468f;
    color: $white;
    font-size: 14px;
    letter-spacing: 1px;
    line-height: 16px;
    text-align: center;
  }
}

.conditions {
  &__item {
    display: inline;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 18px;

    span {
      color: #b82532;
    }
  }
}

.post-date {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  line-height: 18px;
  margin: 1.8rem 0 2.8rem;
}
