.settings {
  background-color: #54468f;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: calc(100vh - 8.2rem);

  position: absolute;

  &--employer {
    background-color: #0373bb;
    border-radius: 35px 0 0 0;
    min-height: 100vh;
    top: 0;

    .settings__list {
      margin-top: 21.3rem;
    }

    .settings__edit {
      top: 13rem;
    }

    .settings__item {
      background-color: #025e9a;

      &--logo {
        position: absolute;
        top: 16px;

        img {
          max-width: 30px;
        }
      }
    }

    .settings__config {
      border-top: 1px solid #025e9a;
    }

    .settings__user-icon {
      background-color: $white;
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        max-width: 40px;
      }
    }
  }

  &.edit-active {
    .modal-overlay {
      transition: all 0.3s ease;
      visibility: visible;
      opacity: 1;
    }
    .settings__edit-item {
      opacity: 1;
      visibility: visible;
      z-index: 11;

     
    }
    .fa-times {
      display: block;
    }
    .fa-plus {
      display: none;
    }
  }

  &__edit {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #f6f5f9;
    position: absolute;
    top: 7rem;
    right: -2rem;
    z-index: 12;

    display: flex;
    justify-content: center;
    align-items: center;

    &-icon {
      width: 33px;
      height: 33px;
      border-radius: 50%;
      font-size: 15px;
      color: #54468f;
      background-color: $white;
      border: 5px solid #54468f;
      display: flex;
      justify-content: center;
      align-items: center;

      &--blue {
        color: #0373bb;
        border: 5px solid #0373bb;
      }

      .fa-times {
        display: none;
      }
    }

    &-item {
      min-width: 233px;
      visibility: hidden;
      opacity: 0;
      height: 72px;
      padding: 1rem 2.2rem 1rem 4rem;
      background-color: #ebeaef;
      transition: opacity 0.3s, visibility 0.5s ease;

      display: flex;
      align-items: center;
      position: absolute;
      top: 11.5rem;
      left: calc(100% + 0.5rem);
    }

    &-link {
      background-color: #ebeaef;
      color: #0373bb;
      font-family: $ff-talent;
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 0.88px;
      line-height: 16px;
      text-transform: uppercase;
      transition: all 0.3s ease;

      &:hover {
        color: #54468f;
      }
    }
  }

  &__list {
    margin-top: 14.2rem;
    padding: 0 1.6rem;
  }

  &__items {
    display: flex;
    flex-direction: column;
  }

  &__item {
    margin-bottom: 30px;
    color: $accent;
    height: 45px;
    width: 45px;
    background-color: #5c4ca1;
    border-radius: 10px;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    i {
      font-size: 30px;
    }



    a {
      color: white;
      opacity: 0.5;
    }

    &:hover a {
      opacity: 1;
    }

    &.active a {
      opacity: 1;
    }
  }

  &__config {
    margin-bottom: 2rem;
    padding-top: 2rem;
    border-top: 1px solid #645a8f;
    display: flex;
    flex-direction: column;
    align-items: center;

    a {
      color: white;
      opacity: 0.5;
    }
  }

  &__user-icon {
    height: 45px;
    width: 45px;
    border-radius: 1rem 0 0 1rem;
    overflow: hidden;
  }
}
