.profile-hero {
  min-height: 29.6rem;
  border-radius: 3.5rem 0 3.5rem 0;
  background: linear-gradient(180deg, rgba(141, 124, 194, 0) 0%, #030303 100%), url("../img/hero-bg.png") no-repeat;
  background-size: cover;
  position: relative;
  top: 1.9rem;
  z-index: 2;

  display: flex;
  align-items: flex-end;
  justify-content: flex-end;

  &__edit {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #f6f5f9;

    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 50%;
    left: -2.25rem;
    transform: translate(0, -50%);

    &-icon {
      width: 33px;
      height: 33px;
      border-radius: 50%;
      font-size: 15px;
      color: #54468f;
      background-color: $white;
      border: 5px solid #54468f;
      display: flex;
      justify-content: center;
      align-items: center;

      &--blue {
        color: #0373bb;
        background-color: $white;
        border: 5px solid #0373bb;  
      }
    }
  }
}

.user {
  background: transparent;
  color: white;
  display: flex;
  align-items: center;
  position: absolute;
  left: 4rem;
  bottom: -2.4rem;

  &__icon {
    margin-right: 1.5rem;
    max-width: 15rem;
    max-height: 15rem;
    border-radius: 2rem 0 2rem 0;
    overflow: hidden;
    box-shadow: 1rem;
  }

  &__name {
    text-transform: uppercase;
    margin-bottom: 0;
  }

  &__location {
    text-transform: uppercase;
  }
}
.profile-content {
  display: flex;
}
