.dashboard {
  padding: 64px 0;

  p {
    font-family: $ff-talent;
  }
  &__overlay {
    background-color: #ebeaea;
    height: 100%;
    min-height: 100vh;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
  }

  &__image {
    background-image: url(../img/sidebar.png);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    width: 25%;
  }

  .container {
    position: relative;
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 0 26px 0;
    border-radius: 30px 0 30px 0;
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 0 0 15px 10px rgba(84, 70, 143, 0.1);
  }

  &__header {
    padding: 10px 20px 10px 30px;
    display: flex;
    align-items: center;
    background-color: #fff;
    border-radius: 30px 0 0 0;
  }

  &__search {
    padding-left: 48px;
    border-left: 1px solid rgba(84, 70, 143, 0.5);
    max-height: 31px;
    display: flex;
    align-items: center;
  }

  &__search-btn {
    color: #54468f;
    opacity: 0.5;
    font-size: 20px;
    background-color: transparent;
  }

  input.dashboard__search-input {
    border: 0;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 19px;

    &::-webkit-input-placeholder,
    &::-moz-placeholder,
    &:-moz-placeholder,
    &:-ms-input-placeholder {
      opacity: 0.5;
      color: #4a4a4a;
    }

    &:focus {
      box-shadow: none;
    }
  }

  &__user-nav {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  &__logo {
    flex-shrink: 0;
    max-width: 66px;
    margin-right: 36px;
  }

  &__notification {
    margin: 0 10px;
    position: relative;
  }

  &__notification-icon {
    font-size: 20px;
    opacity: 0.5;
    color: #54468f;
  }

  &__notification-count {
    min-height: 12px;
    min-width: 20px;
    padding: 2px 4px;
    color: #ffffff;
    font-family: $ff-talent;
    font-size: 8px;
    font-weight: bold;
    letter-spacing: 0.5px;
    line-height: 9px;
    border-radius: 6px;
    background-color: #54468f;

    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    top: -3px;
    right: -12px;
  }

  &__user {
    margin-right: 30px;
    margin-left: 10px;
  }

  &__user-select {
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 19px;
  }

  &__user-icon {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: #d8d8d8;
  }

  &__main-block {
    padding: 0 50px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__right-block {
    padding: 0 10px;
    width: 25%;
    max-width: 336px;

    background-color: #fff;
    display: flex;
    flex-direction: column;
  }

  &__left-block {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-right: 15px;
  }
}

.dashboard-nav {
  display: flex;
  align-items: center;

  &__list {
    min-height: 31px;
    padding-left: 36px;
    border-left: 1px solid rgba(84, 70, 143, 0.5);

    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 50px;
    display: flex;
    align-items: center;
    position: relative;

    i {
      margin-left: 10px;
      opacity: 0.5;
      font-size: 15px;
      padding-bottom: 5px;
      color: #4a4a4a;
    }

    &.active,
    &:hover {
      .dashboard-nav__link {
        color: #54468f;
        opacity: 1;
      }
      .dashboard-nav__link::after {
        width: 80%;
      }
      i {
        opacity: 1;
        color: #54468f;
      }
    }
    &.active {
      .dashboard-nav__link {
        font-weight: bold;
      }
    }

    .dashboard-nav__dropdown {
      display: none;
      position: absolute;
      top: calc(100% + 16px);
      left: -16px;

      padding: 20px;
      background-color: #ffffff;
      box-shadow: 0 7px 10px 0 rgba(84, 70, 143, 0.29);
      min-width: 160px;
      z-index: 1;
    }

    .dashboard-nav__dropdown a {
      opacity: 0.5;
      color: #4a4a4a;
      font-family: $ff-talent;
      font-size: 16px;
      letter-spacing: 1px;
      line-height: 19px;
      text-transform: uppercase;
      display: block;
      transition: all .3s ease;

      &:not(:last-child) {
        margin-bottom: 12px;
      }

      &:hover {
        opacity: 1;
        color: #54468f;
        font-weight: bold;
      }
    }
  }

  &__link {
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 19px;
    text-transform: uppercase;
    opacity: 0.5;
    transition: all 0.3s ease;

    position: relative;

    &:after {
      content: "";
      height: 3px;
      width: 0;
      position: absolute;
      top: 100%;
      left: 0;
      background-color: #54468f;
      transition: all 0.3s ease;
    }
  }

  &__search {
    display: flex;
  }
}
