.talent-nav {
  border-bottom: 1px solid rgba(84,70,143,0.1);
  background-color: #efefef;
  border-radius: 3.5rem 0 0 0;
  // max-width: 180rem;
  position: fixed;
  margin: auto;
  left: 0;
  right: 0;
  z-index: 999;
  transition: all 0.7s ease;
  .container {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  &__logo {
    max-width: 53px;
    position: absolute;
    top: 50%;
    left: 1.5rem;
    transform: translate(0, -50%);
  }
}

.search-anything {
  width: 65%;
  max-width: 60rem;
  // border: 1px solid rgba(84, 70, 143, 0.1);
  border-radius: 0.5rem;

  display: flex;

  input.search-anything__input {
    width: 100%;
    padding: 1.7rem 2.5rem;
    background-color: #efefef;
    border-radius: 0.5rem 0 0 0.5rem;
    font-weight: bold;
    color: #4a4a4a;
  }

  ::-webkit-input-placeholder {
    text-transform: uppercase;
  }
  ::-moz-placeholder {
    text-transform: uppercase;
  } /* Firefox 19+ */
  :-moz-placeholder {
    text-transform: uppercase;
  } /* Firefox 18- */
  :-ms-input-placeholder {
    text-transform: uppercase;
  }

  &__button {
    border: 1px solid rgba(84, 70, 143, 0.1);
    border-radius: 0 5px 5px 0;
    background-color: #e2e1e6;
    color: #54468f;
    font-size: 3rem;
    padding: 0 2.2rem;

    &--blue {
      color: #0373bb;
    }
  }
}

.user-nav {
  position: relative;
  &__icon {
    padding-right: 1rem;
    color: #54468f;
    font-size: 3.6rem;
    cursor: pointer;

    &--blue {
      color: #0373bb;
    }
  }

  &__notification {
    display: block;
    width: 1.9rem;
    height: 1.9rem;
    border-radius: 50%;
    border: 1px solid #ffffff;
    background: linear-gradient(180deg, #f5515f 0%, #9f041b 100%);
    box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.5), inset 0 -1px 3px 0 #ffffff, 1px 2px 1px 0 rgba(0, 0, 0, 0.16);
    color: white;

    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    right: 0;
    bottom: 0;
  }
}
