.no-search .select2-search {
  display: none;
}

.select2-results__option[aria-selected] {
  color: $text-color;
  font-family: $ff-text;
  font-size: 1.6rem;
  letter-spacing: 0.333rem;
  line-height: 1.9rem;
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
  border-color: transparent transparent #888;
  border-width: 0 0.4rem 0.8rem;
}

.select-text {
  display: flex;
  align-items: center;
  color: $text-color;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
  border-color: #fa4a28 transparent transparent;
  border-width: 0.8rem 0.4rem 0;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

.select-img {
  width: 6rem;
  padding-right: 2rem;
  line-height: 2.8rem;
}

.sign-up__options,
.login__options {
  .select2-container {
    font-family: $ff-text;
    min-width: 16.1rem;
  }

  .select2-container .select2-selection--single {
    height: 4rem;
  }
  .select2-container--default .select2-selection--single {
    background-color: transparent;
    border: 0;
    border-radius: 0;
  }
  .select2-container--default .select2-selection--single .select2-selection__rendered {
    line-height: 4rem;
    padding: 0;
  }

  .select2-container--default .select2-selection--single .select2-selection__arrow {
    height: 4rem;
  }
}

.contact-form,
.position,
.resume__columns {
  .select2-container {
    font-family: $ff-text;
    min-width: 80%;
  }

  .select2-container .select2-selection--single {
    height: 5rem;
    border: 1px solid rgba(151, 151, 151, 0.2);
    border-radius: 0.5rem;
    background-color: #ffffff;
    box-shadow: 0 0.2rem 1rem 0.5rem rgba(0, 0, 0, 0.03);
  }

  .select2-container--default .select2-selection--single .select2-selection__rendered {
    line-height: 5rem;
    padding: 0 2rem;
  }

  .select2-container--default .select2-selection--single .select2-selection__arrow {
    height: 5rem;
    right: 1rem;
  }
}

.vacancy-form__item {
  .select2-container {
    font-family: $ff-text;
    min-width: 80%;
    
  }

  .select2-container .select2-selection--single,
  span.select2-container .select2-selection--multiple {
    height: 4.5rem;
    border: 1px solid rgba(151, 151, 151, 0.2);
    border-radius: 0.5rem;
    background-color: #ffffff;
    box-shadow: 0 0.2rem 1rem 0.5rem rgba(0, 0, 0, 0.03);
  }

  .select2-container--default .select2-selection--single .select2-selection__rendered {
    line-height: 4.5rem;
    padding: 0 5rem 0 2.5rem;
  }

  .select2-container--default .select2-selection--single .select2-selection__arrow,
  .select2-container--default .select2-selection--multiple .select2-selection__arrow {
    height: 3rem;
    width: 3rem;
    padding-top: 3px;
    border-radius: 50%;
    background-color: #0373bb;
    color: $white;

    display: flex;
    align-items: center;
    justify-content: center;

    top: 50%;
    right: 1rem;
    transform: translate(0, -50%);
    transition: all 0.3s ease;

    &::before {
      content: "\f078";
      font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands";
      font-weight: 800;
    }
  }

  .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
    transform: translate(0, -50%) rotate(180deg);
  }

  .select2-container--default .select2-selection--single .select2-selection__arrow b {
    display: none;
  }


}

.multiple-select-container {
  width: 100%;
  line-height: 45px;

  .select2-container {
    min-width: 100%;
  }

  .select2-container--default.select2-container--open .select2-selection--multiple .select2-selection__rendered:after {
    transform: translate(0, -50%) rotate(180deg);
  }
  span.select2-container--default .select2-selection--multiple .select2-selection__rendered {
    padding-left: 2.5rem;
    color: #4a4a4a;
    &::after {
      content: "\f078";
      font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands";
      font-weight: 800;
      height: 3rem;
      width: 3rem;
      padding-top: 3px;
      border-radius: 50%;
      background-color: #0373bb;
      color: $white;
      cursor: pointer;

      display: flex;
      align-items: center;
      justify-content: center;

      position: absolute;
      top: 50%;
      right: 1rem;
      transform: translate(0, -50%);
      transition: all 0.3s ease;
    }
  }

  .select2-selection--multiple--custom.select2-selection--custom {
    background-color: transparent;
    border: none;
    box-shadow: none;
  }

  .select2-container--default .select2-selection--multiple .select2-selection__choice {
    margin-right: 1.5rem;
    margin-top: 1rem;
    border: none;
    border-radius: 10px 0 10px 0;
    background-color: #025e9a;
    padding-left: 1rem;
    line-height: 3rem;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    color: $white;
    text-transform: uppercase;

    font-family: $ff-talent;
    font-size: 14px;
    letter-spacing: 1px;
  }

  .select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    margin-left: 1.6rem;
    margin-right: 0;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background-color: #fcfcfc;
    color: #025e9a;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.8rem;
  }
}
