.btn,
.ui-widget-content a.btn {
  display: inline-block;
  min-width: 17rem;
  padding: 1.7rem;
  border-radius: 5rem;
  background-color: $button-bg;
  border: solid 2px $button-bg;
  color: $white;
  font-size: 1.8rem;
  font-family: $ff-text;
  line-height: 1.2;
  letter-spacing: 0.375rem;
  text-align: center;
  transition: all 0.7s ease;
  cursor: pointer;

  &--rectangle {
    min-width: 200px;
    padding: 1.5rem 0.5rem;
    border-radius: 0;
    font-family: $ff-talent;
    font-size: 1.6rem;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 1.9rem;
    text-transform: uppercase;
    box-shadow: 0 2px 10px 5px rgba(0, 0, 0, 0.2);
  }

  &.red {
    box-shadow: none;
    background-color: $red;
    border-color: $red;

    &:hover {
      color: $red;
    }
  }

  &--max {
    min-width: 40.9rem;
    padding: 4rem;
    border-radius: 5.8rem;
    font-size: 2.4rem;
    line-height: 3.4rem;
    letter-spacing: 0.5rem;

    margin: 7.3rem 0 2rem 0;
  }

  &:hover {
    background-color: transparent;
    color: $button-bg;
  }

  &[disabled]:hover {
    background-color: $accent;
    color: $white;
  }
}

.prevBtn {
  opacity: 0.5;
}

.link,
.ui-widget-content a.link {
  font-size: 1.8rem;
  line-height: 2.2rem;
  letter-spacing: 0.375rem;
  color: $accent;
  text-decoration: underline;
  text-transform: uppercase;
  transition: all 0.3s ease;

  &--small {
    font-size: 1.4rem;
    letter-spacing: 0.88px;
    line-height: 1.7rem;
    text-transform: none;
  }

  &:hover {
    text-decoration: none;
  }
}

.small-link {
  font-family: $ff-text;
  font-size: 1.6rem;
  font-weight: 500;
  letter-spacing: 0.333rem;
  line-height: 2.8rem;
  color: $accent;
  transition: all 0.3s ease;
  &:hover {
    opacity: 0.5;
  }
}

.nav-account__link:hover {
  color: $accent;
}

.post-btn,
a.post-btn,
button.post-btn {
  padding: 5px 10px;
  min-width: 74px;
  background-color: #54468f;
  color: #ffffff;
  font-family: $ff-talent;
  font-size: 11px;
  line-height: 13px;
  text-transform: uppercase;
  cursor: pointer;

  &--blue {
    background-color: #0374bb;
  }

  &:hover {
    opacity: 0.7;
  }
}

.clear,
a.clear,
button.clear {
  padding: 5px 10px;
  color: #b82532;
  font-family: $ff-talent;
  font-size: 11px;
  line-height: 13px;
  text-decoration: underline;
  transition: all 0.3s;

  &:hover {
    text-decoration: none;
  }
}
.form__talent .nextBtn {
  background-color: #54468f;
  color: white;

  &[disabled] {
    background-color: white;
    color: #54468f;
  
    &:hover,
    .ui-widget-content a.btn[disabled]:hover {
      background-color: white;
    }
  }  
}



