.sign-up {
  background-color: $bg-signUp;
  padding: 5.3rem 2rem;
  min-height: 100vh;

  color: $text-color;
  font-family: $ff-text;
  font-size: 1.6rem;
  letter-spacing: 1px;
  line-height: 1.9rem;

  &__container {
    max-width: 120rem;
    margin: 0 auto;
  }

  &__options {
    display: flex;
    justify-content: space-between;
    align-items: center;

    letter-spacing: 0.333rem;
  }

  .small-link {
    letter-spacing: 0.296rem;
    border-bottom: solid 1px $accent;
    &:hover {
      opacity: 1;
      border-bottom: solid 1px transparent;
    }
  }
}



.tabs {
  padding: 0;
  border-radius: 0;
  border: 1px solid rgba(151, 151, 151, 0.1);
  background-color: $white;
  box-shadow: 0 2px 10px 15px rgba(85, 52, 52, 0.03);

  .ui-tabs-panel {
    padding: 3rem 0 0;
  }
  .ui-state-active,
  .ui-widget-content .ui-state-active,
  .ui-widget-header .ui-state-active {
    background-color: $white;
    border: 0;

    .ui-tabs-anchor {
      width: 100%;
      color: $text-color;
    }
  }

  .ui-state-default {
    
    background-color: #d8d8d8;
    border: 0;

    .ui-tabs-anchor {
      width: 100%;
    }
  }

  .ui-corner-all {
    border-radius: 0;
  }
  .ui-tabs-nav {
    padding: 0;
    border: 0;

    li {
      margin: 0;
      border-radius: 0;
      &.ui-tabs-active {
        margin: 0;
      }
      &:not(:last-child){
        border: 1px sollid #fff !important;
      }
    }
  }

  &__navigation-list {
    margin-bottom: 3rem;
    display: flex;
  }

  &__navigation-item {
    flex-grow: 1;
    padding: 0 2rem;
    text-align: center;
    background-color: #d8d8d8;
  }

  &__navigation-link {
    color: $text-color;
    font-family: $ff-text;
    font-size: 1.6rem;
    font-weight: 500;
    letter-spacing: 0.333rem;
    line-height: 5rem;
    transition: all 0.3s;

    &:hover {
      color: $accent;
    }


  }

  &__title {
    padding: 0 4.8rem 2.1rem;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);

    text-align: center;
    font-size: 1.6rem;
    letter-spacing: 1px;
    line-height: 1.9rem;

    &--left {
      text-align: left;
    }
    h2 {
      font-weight: 300;
      letter-spacing: 1px;
      line-height: 4.4rem;
      margin-bottom: 1.7rem;
    }
  }
}

.form__talent {
  color: $text-color;
  .social-links {
    font-family: $ff-text;
    padding: 3rem 4.8rem;
    display: flex;
    justify-content: space-between;

    @include sm-max {
      justify-content: center;
    }

    &__item {
width: 30%;
      padding: 1rem;
      background-color: #3a5797;
      text-align: center;

      &:not(:last-child) {
        margin-right: 2.2rem;
      }

      &:nth-child(2) {
        background-color: #4086f5;
      }

      .social-link {
        color: white;
        font-size: 3rem;
        display: flex;
        align-items: center;
        justify-content: center;
        span {
          font-size: 1.8rem;
          letter-spacing: 0.375rem;
          line-height: 3rem;
          margin-left: 3.9rem;

          @include lg-max {
            margin-left: 1.5rem;
          }
        }
      }
    }
  }
}

#employer-contact-form {
  padding-top: 4rem;

  .contact-form__agreement {
    padding-top: 4rem;
  }
}

.delimiter {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;

  .line {
    width: 10rem;
    height: 1px;
    background-color: #979797;
    margin: 0 0.9rem;
  }
}
