.resume-review {
  margin-bottom: 1.5rem;
  padding: 1rem 1.5rem;
  border: 1px solid rgba(84, 70, 143, 0.1);
  background-color: #efefef;

  display: flex;
  justify-content: space-between;
  align-items: center;

  &__title {
    display: flex;
    align-items: center;
  }

  &__icon {
    font-size: 1.6rem;
    margin-right: 1.5rem;
    width: 3rem;
    height: 3rem;
    border-radius: 1rem 0 1rem 0;
    background-color: $red;
    color: white;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__close {
    font-size: 2rem;
    width: 2rem;
    height: 2rem;
    color: $red;
  }
}

.talent-resume {
  min-width: 40%;
  margin-right: 2rem;
}
.biography {
  padding: 2.5rem 1.5rem;
  border: 1px solid rgba(84, 70, 143, 0.1);
  background-color: #efefef;

  &--employer {
   
    & .biography__icon,
    & .biography__icon--main {
      background-color: #0373BB;
    }
  }

  &__box {
    margin: 1.5rem 0;
    display: flex;
    align-items: flex-start;

    &--employer {
      align-items: center;
      justify-content: space-between;
    }
  }

  &__icon {
    margin-right: 1rem;
    width: 30px;
    height: 30px;
    flex-shrink: 0;
    border-radius: 10px 0 10px 0;
    background-color: #54468f;
    color: white;

    display: flex;
    justify-content: center;
    align-items: center;

    &--main {
      border-radius: 10px;
    }

    i {
      font-size: 16px;
    }
  }

  &__title-box {
    display: flex;
    align-items: center;

    &--top {
      align-items: flex-start;
    }

    h3 {
      color: #4a4a4a;
      font-family: $ff-talent;
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 0.88px;
      line-height: 16px;
      text-transform: uppercase;
    }
  }

  &__text-box {
    text-align: right;
    p {
      color: #b82532;
      font-family: $ff-talent;
      font-size: 14px;
      letter-spacing: 0.88px;
      line-height: 16px;
      text-transform: uppercase;
    }
  }

  &-employer__list {
    margin-top: -1.5rem;
  }

  &-employer__item {
    margin-bottom: 1rem;
    display: flex;
    justify-content: flex-end;

    a.biography-employer__item-text {
      text-decoration: underline;
      color: #0373bb;

      &:hover {
        text-decoration: none;
      }
    }

    &-count {
      min-width: 26%;
      text-align: right;
      color: #b82532;
      font-family: $ff-talent;
      font-size: 14px;
      letter-spacing: 0.88px;
      line-height: 16px;
      text-transform: uppercase;
    }

    &-text {
      color: #4a4a4a;
      font-family: $ff-talent;
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 0.88px;
      line-height: 16px;
      text-transform: uppercase;
    }
  }

  &__title {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 2rem;
    padding-top: 0.5rem;
  }

  a.biography__more-link {
    color: #54468f;
    text-align: right;
    justify-self: right;

    &:hover {
      opacity: 0.5;
    }
  }

  &__line {
    max-width: 16rem;
    margin: 0 auto;
    height: 2px;
    background-color: #e2e1e6;
  }

  &__text {
    display: flex;
    align-items: center;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__item:not(:last-child) {
    margin-bottom: 1.8rem;
  }

  &__dates {
    min-width: 60px;
    margin-left: 1rem;
    font-weight: bold;
  }

  &__accent {
    font-weight: bold;
    color: #54468f;
  }

  a.biography__remote {
    font-weight: bold;
    color: #54468f;
    text-transform: uppercase;
  }

  .dates-line {
    width: 10px;
    flex-shrink: 0;
  }
}
