.info-list {
  &__item {
    margin-bottom: 30px;
    position: relative;

    &.active {
      .info-list__content {
        display: block;
      }

      .info-list__button i {
        transform: rotate(180deg);
      }
    }
  }

  &__button {
    position: absolute;
    top: 50%;
    left: -23px;
    transform: translate(0, -50%);
    z-index: 10;

    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: #54468f;
    border: solid 5px #fff;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;

    i {
      font-size: 25px;
      color: $white;
      transition: all .3s ease;
    }
  }

  &__title {
    padding: 22px 22px 21px 80px;
    border: 1px solid #ebecf0;
    background-color: #ffffff;
    position: relative;

    h3 {
      color: #4a4a4a;
      font-family: $ff-talent;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 1px;
      line-height: 19px;
      text-transform: uppercase;
    }
  }

  &__content {
    display: none;
    padding: 15px 20px 20px;
    border: 1px solid #ebecf0;
    background-color: #ffffff;
    box-shadow: 14px 7px 10px 0 rgba(226, 225, 230, 0.5);
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 20;
  }
}
