.light-popup {
  min-height: 50vh;
  position: relative;
  background: #fff;
  padding: 5rem 2rem;
  width: auto;
  max-width: 636px;
  margin: 20px auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h2 {
    text-align: center;
    color: #4a4a4a;
    font-family: $ff-text;
    font-size: 36px;
    font-weight: 300;
    letter-spacing: 1px;
    line-height: 44px;
    margin-bottom: 5rem;
  }
  p {
    text-align: center;
    color: #4a4a4a;
    font-family: $ff-text;
    font-size: 1.6rem;
    letter-spacing: 3px;
    line-height: 25px;
    text-align: center;
  }

  .form-username {
    text-align: center;
    margin-top: 5rem;
    margin-bottom: 6.4rem;
  }

  #email {
    padding: 2rem;
    margin-top: 1.4rem;

    color: #4a4a4a;
    font-family: $ff-text;
    font-size: 1.6rem;
    letter-spacing: 1px;
    line-height: 1.9rem;
    
  }

  label {
    color: #4a4a4a;
    font-family: $ff-text;
    font-size: 1.6rem;
    letter-spacing: 1px;
    line-height: 1.9rem;
  }
}

.mfp-close-btn-in .mfp-close {
  color: transparent;
  background-image: url('../img/lose-icon.png');
  background-size: 30px 30px;
  background-repeat: no-repeat;
  background-position: left bottom;
}

