.steps {
    margin-top: 8px;
    display: flex;
    justify-content: space-between;
  
    @include md-max {
      flex-direction: column;
    }
  
    &--employer {
      max-height: 98rem;
  
      @include md-max {
        max-height: 100%;
      }
    }
  
    &__nav {
      max-width: 40rem;
      width: 100%;
      border: 1px solid rgba(151, 151, 151, 0.2);
      background-color: #efefef;
      letter-spacing: 0.333rem;
  
      &--employer {
        min-height: 98rem;
        background-image: url("../img/employer-menu.png");
        background-repeat: no-repeat;
        background-position: left bottom;
        background-size: cover;
      }
    }
  
    &__menu-item {
      padding-left: 36%;
      position: relative;
  
      &:before {
        content: "";
        height: 2px;
        width: 46px;
        background-color: $accent;
        position: absolute;
        top: 50%;
        left: 19%;
        transform: translate(0, -50%);
      }
  
      &:not(:last-child) .steps__point:before {
        content: "";
        height: 30px;
        width: 2px;
        background-color: $accent;
        position: absolute;
        top: 150%;
        left: 50%;
        transform: translate(50% 0);
      }
    }
  
    &__point {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 25px;
      height: 25px;
      background-color: $white;
      color: white;
      border: 1px solid #fa4a28;
      border-radius: 5px;
      position: absolute;
      top: 50%;
      left: 10%;
      transform: translate(0, -50%);

      &.done {
        background-color: #fa4a28;
      }
    }
  
    &__menu-item:not(:last-child) {
      margin-bottom: 6.4rem;
    }
  
    &__logo {
      max-width: 6.64rem;
      margin: 0 auto;
      padding: 2.5rem 0;
    }
  
    &__menu {
      padding-top: 11rem;
      padding-bottom: 11rem;
      border-top: 1px solid rgba(151, 151, 151, 0.2);
    }
  
    &__form-box {
      min-width: 67%;
      flex-grow: 1;
      background-color: $white;
      padding: 2.7rem;
      border: 1px solid rgba(151, 151, 151, 0.2);
      color: $text-color;
    }
  }