.login {
  background-color: $bg-signUp;
  padding: 5.3rem 2rem;
  min-height: 100vh;

  color: $text-color;
  font-family: $ff-text;
  font-size: 1.6rem;
  letter-spacing: 1px;
  line-height: 1.9rem;

  &__container {
    max-width: 120rem;
    margin: 0 auto;
  }

  &__options {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    letter-spacing: 0.333rem;
  }

  &__box {
    background-color: $white;
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 60%;
    overflow: hidden;

    display: flex;
    align-items: center;

    
    @include sm-max {
      background-size: 85%;
       flex-direction: column; 
    }
  }

  &__text {
    width: 60%;
    min-width: 200px;
    max-width: 400px;
    margin-left: 7.5rem;

    display: flex;
    flex-direction: column;

    @include sm-max {
        min-width: 270px;
    margin: 0 auto;
    }

  }

  &__title {
    font-size: 1.6rem;
    letter-spacing: 1px;
    line-height: 1.9rem;
    margin-top: 6rem;

    @include sm-max {
        text-align: center;
    }

    h2 {
      font-weight: 300;
      letter-spacing: 1px;
      line-height: 4.4rem;
      margin-bottom: 5.4rem;
    }

    .logo {
      max-height: 4rem;
    }
  }

  .social-links {
      display: flex; 

      @include sm-max {
          justify-content: center;
      }
      
      &__item {
          width: 7rem;
          height: 5rem;
          padding: 1rem;
          background-color:#3A5797;
          text-align: center;


          &:not(:last-child){
              margin-right: 3rem;
          }

          &:nth-child(2) {
            background-color:#4086F5;  
          }

          .social-link {
            color: white;
            font-size: 3rem;
            margin: 1rem;
          }      
      }
  }

  &__forgot {
      margin-top: -2rem;
      margin-bottom: 3rem;
      text-align: right;
  }

  .delimiter {
      justify-content: flex-start;
      margin: 3rem 0;

      @include sm-max{
          justify-content: center;
      }

      .line:first-child {
          display: none;
          @include sm-max {
            display: block;  
          }
      }
  }



  &__image {
    margin-top: 7.2rem;
    position: relative;
    right: -10rem;
    bottom: -2rem;

    @include md-max {
        right: -5rem;
        bottom: -5rem;
    }

    @include sm-max {
        margin-top: -10rem;
        position: relative;
        left: 0;
    }
  }


}
