.roles {
  padding-bottom: 12rem;
  background-color: $bg-light;

  color: #4a4a4a;
  font-family: $ff-text;
  font-size: 1.6rem;
  font-weight: 500;
  letter-spacing: 3.33px;
  line-height: 3rem;

  &__container {
    max-width: 144rem;
    margin: 0 auto;
    display: flex;

    @include lg-max {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__box {
    width: 75%;
    padding-left: 2rem;
    display: flex;
    justify-content: space-between;

    @include sm-max {
      flex-direction: column;
      width: 100%;
      padding-left: 0;
    }
  }

  &__list {
    width: 34%;
    border-right: solid 1px rgba(151, 151, 151, 0.5);

    @include lg-max {
      width: 100%;
      padding: 0 2rem;
      margin: 0;
      border: 0;
    }
  }

  &__row {
    padding: 0 0 1.4rem;
    cursor: pointer;
    @include lg-max {
      padding: 1rem 0;
      border-bottom: solid 1px rgba(151, 151, 151, 0.5);
    }

  }

  &__item {
    padding-right: 1.5rem;
    position: relative;

    &.active {
      font-weight: bold;
    }

    @include lg-max {
      font-size: 14px;
    }

    @include sm-max {
      padding-right: 9rem;
    }
  }

  &__item::after {
    transition: all 0.3s;

    @include lg {
      display: none;
    }

    @include lg-max {
      content: ">";
      color: rgba(151, 151, 151, 0.9);
      position: absolute;
      right: 4rem;
      top: 50%;
      transform: translate(0, -50%) rotate(90deg);
    }
  }

  &__item.active::after {
    content: ">";
    color: $accent;
    position: absolute;
    right: 4rem;
    top: 50%;
    transform: translate(0, -50%);

    @include lg-max {
      transform: translate(2px, -50%) rotate(-90deg);
    }
  }

  .sub-title {
    min-height: 2.8rem;
    margin-bottom: 2.2rem;
  }

  &__img {
    max-width: 33rem;
    max-height: 35rem;

    @include lg-max {
      margin-top: 5rem;
    }
  }
}

.specialities {
  width: 66%;
  margin: 0 0 0 1.3rem;

  @include lg-max {
    display: none;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;

    @include lg-max {
      margin-top: 1.5rem;
    }
  }

  &__item {
    padding-right: 1.5rem;
    padding-bottom: 1.3rem;
    width: 50%;

    @include lg-max {
      width: 100%;
      margin: 0 3.5rem 0 5rem;
    }
  }
}
