h1 {
    font-family: $ff-text;
    font-size: 6.4rem;
    line-height: 1.4;
    font-weight: bold;

    @include xl-max {
        font-size: 5.4rem;
    }
}

h2 {
    font-family: $ff-text;	
    font-size: 3.6rem;	
    line-height: 3.9rem;
    font-weight: bold;	
    
}

h3 {
    font-family: $ff-text;
    font-size: 2.4rem;
    line-height: 2.8rem;
    font-weight: 500;
    letter-spacing: 0.5rem;
}
p {
    font-family: $ff-text;
    font-size: inherit;
    font-weight: normal;
}

.sub-title {
    text-transform: uppercase;
    color: $accent;

    &--white {
        color: $white;
    }
}

.accent {
    color: $accent;
}