.profile-menu {
  padding: 4.8rem 3.5rem 1rem 25%;
  top: -2rem;
  border: 0;
  background-color: #efefef;
  border: 1px solid rgba(84, 70, 143, 0.1);
  margin-bottom: 1.5rem;
  min-height: 8.2rem;

  &--employer {
    padding: 0;
    display: flex;
    justify-content: flex-end;

  }

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    padding: 0 2rem;
    border-right: 1px solid #e2e1e6;
    font-family: $ff-talent;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0.88px;
    line-height: 16px;
    text-align: left;
    text-transform: uppercase;

    a {
      color: $tcolor;
      transition: all 0.3s ease;

      &:hover {
        color: #54468f;
      }
    }

    &:first-child a {
      color: #54468f;
    }

    &--accent a {
      color: #b82532;
    }
  }

  &__box {
    width: 80%;
    padding: 3.1rem 0 1.1rem 1rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__opportunities {
    display: flex;
    align-items: center;

    p {
      color: #0373bb;
      font-family: $ff-talent;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 1px;
      line-height: 19px;
      text-transform: uppercase;
    }

    span {
      margin-left: 11px;
      font-size: 22px;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: #b82532;
      color: $white;

      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__burger-nav {
    padding-right: 2.2rem;
    position: relative;
    z-index: 100;
    cursor: pointer;
  }

  &__burger {
    font-size: 23px;
    color: #0373bb;

    &.open {
      i:first-child {
        display: none;
      }
      i:last-child {
        display: block;
      }
    }

    i:last-child {
      display: none;
    }
  }
}

.menu-dropdown {
  display: none;
  min-width: 12.6rem;
  padding: 2rem;
  text-align: right;
  border: 1px solid rgba(84, 70, 143, 0.1);
  border-radius: 0 0 5px 5px;
  background-color: #efefef;
  box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.05);

  position: absolute;
  top: calc(100% + 1.4rem);
  right: 0;

  &__item {
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0.88px;
    line-height: 16px;
    text-transform: uppercase;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }

    &:hover a {
      color: #0373bb;
    }

    a {
      color: #4a4a4a;
    }
  }
}
