.employer,
.talent {
  .contacts {
    height: calc(100vh - 8.2rem);
    padding: 1rem;
    border: 1px solid rgba(84, 70, 143, 0.1);
    background-color: #efefef;

    &__box {
      margin-top: 3.2rem;
      padding: 0 0.6rem;
    }

    &__title {
      font-weight: bold;
      text-transform: uppercase;
    }

    &__list {
      margin-top: 1.7rem;
    }

    &__item {
      margin-bottom: 1rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  .contact {
    display: flexbox;
    align-items: center;

    position: relative;

    &__icon {
      max-width: 3rem;
      max-height: 3rem;
      border-radius: 50%;
      overflow: hidden;
    }

    &__notification {
      display: block;
      width: 1.9rem;
      height: 1.9rem;
      border-radius: 50%;
      border: 1px solid #ffffff;
      background: radial-gradient(circle, #735ecb 0%, #54468f 100%);
      box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.5),
        inset 0 -1px 3px 0 #ffffff, 1px 2px 1px 0 rgba(0, 0, 0, 0.16);
      color: white;

      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      right: -0.5rem;
      bottom: -0.5rem;
    }

    &__name {
      margin: 0 2.2rem;
      flex-grow: 1;
      font-weight: normal;
    }

    &__status {
      display: block;
      min-width: 1rem;
      min-height: 1rem;
      border-radius: 50%;

      &--green {
        background-color: $green;
      }

      &--yellow {
        background-color: $yellow;
      }

      &--gray {
        background-color: $gray;
      }

      &--red {
        background-color: $red;
      }
    }
  }
}
