.tab {
  display: none;
  &:first-child {
    display: block;
  }
}

.start-date {
  position: relative;

  &__img {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translate(0, -50%);
  }
}

#start-date {
  max-width: 350px;
}

::-webkit-input-placeholder {
  opacity: 0.5;
}
::-moz-placeholder {
  opacity: 0.5;
} /* Firefox 19+ */
:-moz-placeholder {
  opacity: 0.5;
} /* Firefox 18- */
:-ms-input-placeholder {
  opacity: 0.5;
}
.select-input {
  display: flex;

  input {
    font-family: $ff-text;
    width: 100%;
    height: 5rem;
    padding-left: 2rem;
    border: 1px solid rgba(151, 151, 151, 0.2);
    border-radius: 5px;
    background-color: #ffffff;
    -moz-appearance: textfield;
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  span.select2-container {
    margin-bottom: 0;
    min-width: 7rem;
    .select2-selection--single {
      border: solid 1px rgba(151, 151, 151, 0.2);
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      box-shadow: none;
      background-color: rgba(151, 151, 151, 0.2);
      font-family: $ff-text;
    }
  }
}

form {
  input[type="text"],
  input[type="email"],
  input[type="tel"],
  input[type="password"] {
  font-family: $ff-text;
    height: 5rem;
    width: 100%;
    border: 1px solid rgba(151, 151, 151, 0.2);
    border-radius: 0.5rem;
    background-color: #ffffff;
    padding: 2rem;

    &:focus {
      box-shadow: 0 0.2rem 1rem 0.5rem rgba(0, 0, 0, 0.03);
    }
  }

  input.other-text {
    min-width: 18rem;
    border: 0;
    height: auto;
    box-shadow: none;
    padding: 0.4rem;
    border-radius: 0;
    border-bottom: 1px solid $accent;
    background-color: transparent;
    &:focus {
      box-shadow: none;
    }
  }

  input.select-option {
    margin-left: 1rem;
    width: 18rem;
    height: 1.7rem;
    text-align: center;
    border: 0;
    box-shadow: none;
    padding: 0.4rem;
    border-radius: 0;
    border-bottom: 1px solid $accent;
    background-color: transparent;
    &:focus {
      box-shadow: none;
    }

    &::-webkit-input-placeholder {
      opacity: 1;
      color: $accent;
    }
    &::-moz-placeholder {
      opacity: 1;
      color: $accent;
    } /* Firefox 19+ */
    &:-moz-placeholder {
      opacity: 1;
      color: $accent;
    } /* Firefox 18- */
    &:-ms-input-placeholder {
      opacity: 1;
      color: $accent;
    }
  }
}

.main-checkbox {
  display: inline-block;
  min-width: 2.5rem;
  min-height: 2.5rem;
  margin-right: 1rem;
  border: 1px solid #979797;
  border-radius: 0.5rem;
  position: relative;

  &::after {
    content: "\f00c";
    font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands";
    font-weight: 900;
    display: block;
    font-size: 1rem;
    color: $white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: opacity 0.2s;
  }
}

.main-radio-checkbox {
  min-width: 2.5rem;
  min-height: 2.5rem;
  margin-right: 2rem;
  display: inline-block;
  border: 1px solid #979797;
  border-radius: 50%;
  position: relative;

  &::after {
    content: "\f00c";
    font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands";
    font-weight: 900;
    display: block;
    font-size: 1rem;
    color: $white;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: opacity 0.2s;
  }
}

input[type="checkbox"],
input[type="radio"] {
  display: none;
}

input[type="checkbox"]:checked ~ .main-checkbox,
input[type="radio"]:checked ~ .main-radio-checkbox {
  background-color: #4a4a4a;
}

input[type="checkbox"]:checked ~ .main-checkbox::after,
input[type="radio"]:checked ~ .main-radio-checkbox::after {
  opacity: 1;
}

.login__form {
  display: flex;
  flex-direction: column;
  input[type="text"],
  input[type="password"],
  input[type="email"] {
    margin-top: 1.4rem;
    margin-bottom: 3rem;
  }

  .form__checkbox {
    display: flex;
    align-items: center;
    margin-bottom: 6rem;
  }

  .btn {
    align-self: flex-end;
    margin-bottom: 3rem;

    @include sm-max {
      margin-bottom: 0;
    }
  }
}

.form__checkbox {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 2.5rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  label {
    font-family: $ff-text;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4.8rem;
    margin: 1rem 0;
  }

  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="tel"] {
    width: 51rem;
    max-width: 80%;

    @include sm-max {
      width: 100%;
      margin-top: 1rem;
    }
  }

  &__agreement {
    padding: 2.6rem 0 3rem;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    .form__checkbox {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      margin-bottom: 2rem;

      p {
        width: 80%;
      }
    }
  }

  .form__checkbox {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 2rem;

    p {
      width: 80%;
    }
  }

  .btn {
    align-self: flex-end;
    margin: 3rem 4.8rem 3.5rem 0;
  }
}

.form__employer {
  display: flex;
  flex-direction: column;

  label {
    font-family: $ff-text;
    display: flex;
    align-items: center;
  }

  .contact-form label {
    @include sm-max {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  label.form__checkbox {
    flex-direction: row;
  }

  .main-checkbox {
    min-width: 2.5rem;
    min-height: 2.5rem;
  }
}
