.map {
  background-color: $bg-light;
  margin-top: 13.7rem;

  @include md-max {
      margin-top: 0;
  }

  &__container {
    max-width: 152.6rem;
    margin: 0 auto;
    padding: 10.9rem 0 6rem;

    display: flex;
    justify-content: space-between;
    align-items: center;

    @include lg-max {
      flex-direction: column;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__content {
    max-width: 35rem;
    padding-left: 3rem;

    font-size: 1.8rem;
    line-height: 2.6rem;
    color: $text-color;
    font-weight: bold;

    span {
      color: $accent;
    }
  }

  &__img-right {
    padding-left: 3rem;

  }

  &__img-left {
    padding-left: 2rem;
    padding-right: 3rem;

    @include md-max {
        padding: 0 2rem;
    }
  }

  &__title {
    color: $text-color;
    margin: 3rem 0;
  }

  &__element img {
    max-height: 1.6rem;
    margin-top: 3rem;
  }
}
