.footer {
  background-color: $bg-dark;
  padding: 13.5rem 0;
  color: $white;

  font-size: 1.6rem;
  font-weight: 500;
  letter-spacing: 0.333rem;
  line-height: 2.8rem;

  &__container {
    max-width: 123.4rem;
    margin: 0 auto;
    padding: 0 4rem;
    display: flex;
    justify-content: space-between;

    @include sm-max {
      flex-direction: column;
      align-items: center;
    }
  }

  &__column:not(:last-child) {
    margin-right: 4rem;

    @include sm-max {
      margin-right: 0;
      margin-bottom: 5rem;
    }
  }

  &__column {
    @include sm-max {

      text-align: center;
    }
  }

  .sub-title {
    margin-bottom: 3.9rem;

    @include sm-max {
      text-align: center;
      margin-bottom: 1.5rem;
    }
  }

  p {
    margin-bottom: 3.7rem;

    @include sm-max {
      text-align: center;
      margin-bottom: 1.5rem;
    }
  }
}
