.dashboard-info {
  margin-right: 15px;
  padding-bottom: 77px;
  background-color: #ffffff;

  &-line {
    margin: 24px auto 0;
    width: 67%;
    height: 1px;
    background-color: #b82532;
  }

  &__talents {
    background-color: $white;
    padding: 20px 0 0 0;
    position: relative;

    &.active {
      box-shadow: 14px 7px 10px 0 rgba(226, 225, 230, 0.5);
      .dashboard-info__list--more {
        display: block;
        box-shadow: 14px 7px 10px 0 rgba(226, 225, 230, 0.5);
      }

      .dashboard-info__button {
        transform: rotate(180deg);
      }
    }
  }

  &__employer {
    background-color: $white;
    padding: 25px 5px 0 5px;
    position: relative;

    .select-action {
      background-color: #0374bb;
    }

    .dashboard-info__button {
      background-color: #0374bb;
    }

    &.active {
      box-shadow: 14px -7px 10px 0 rgba(226, 225, 230, 0.5);
      top: -80px;
      .dashboard-info__list--more {
        display: block;
        box-shadow: 14px 10px 10px 0 rgba(226, 225, 230, 0.5);
      }

      .dashboard-info__button {
        transform: rotate(180deg);
      }
    }
  }

  &__button {
    position: absolute;
    top: 75px;
    left: -23px;
    z-index: 10;
    
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: #54468f;
    border: solid 5px #c9b3dd;
    transition: all .3s ease;

    display: flex;
    align-items: center;
    justify-content: center;

    i {
      font-size: 25px;
      color: $white;
    }
  }

  &__menu {
    padding: 0 20px 12px 60px;
    border-bottom: 1px solid #ebecf0;
    display: flex;
    align-items: center;
  }

  &__title {
    padding-right: 10px;
    opacity: 0.5;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;

    &:first-child {
      width: 14%;
    }
    &:nth-child(2) {
      width: 12%;
    }
    &:nth-child(3) {
      width: 11%;
    }
    &:nth-child(4) {
      width: 15%;
    }
    &:nth-child(5) {
      width: 18%;
    }
    &:nth-child(6) {
      width: 18%;
    }
    &:last-child {
      padding-right: 0;
      flex-grow: 1;
      align-self: flex-end;
      text-align: right;
    }
  }

  &__list {
    padding: 24px 5px 0 5px;
    position: relative;

    &:last-child {
      padding-bottom: 16px;
    }
    &--more {
      display: none;
      position: absolute;
      width: 100%;
      background-color: white;
      top: 100%;
      right: 0;
      left: 0;
      z-index: 9;
    }
  }

  &__item {
    padding: 0 15px 0 60px;
    display: flex;
    align-items: center;

    &:not(:last-child) {
      padding-bottom: 20px;
    }
  }

  &__item-text {
    padding-right: 10px;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    line-height: 19px;
    word-wrap: break-word;

    &:first-child {
      width: 14%;
    }
    &:nth-child(2) {
      width: 12%;
    }
    &:nth-child(3) {
      width: 11%;
    }
    &:nth-child(4) {
      width: 15%;
    }
    &:nth-child(5) {
      width: 18%;
    }
    &:nth-child(6) {
      width: 18%;
    }
    &:last-child {
      padding-right: 0;
      flex-grow: 1;
      text-align: right;
    }
  }

  &__email {
    span {
      color:#0374BB;
    }

    &--purpule {
      span {
        color: #54468F;
      }
    }
  }

  &__all-list {
    position: absolute;
    right: 20px;
    bottom: 13px;
  }

  &__link {
    color: #0374bb;
    font-family: $ff-talent;
    font-size: 14px;
    line-height: 16px;
    text-decoration: underline;
    transition: all 0.3s ease;

    &:hover {
      text-decoration: none;
    }
  }
}
