.posting-modal {
  display: none;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);

  &__box {
    border: none;
    max-width: 60rem;
    border-radius: 30px 0 30px 0;
    background-color: #fcfcfc;
    margin: 15% auto;
    width: 80%;
    overflow: hidden;
  }

  &__header {
    padding: 2.3rem 1.5rem 2.2rem 3rem;
    background-color: #0373bb;
    color: white;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__title {
    color: #fcfcfc;
    font-family: $ff-talent;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 19px;
    text-transform: uppercase;
  }

  &__close {
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #fcfcfc;
    color: #0373bb;
    font-size: 40px;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__close:hover,
  &__close:focus {
    color: black;
    text-decoration: none;
  }

  &__content {
    padding: 4.9rem 2.8rem;
  }

  &__link {
    color: #0373bb;
    font-family: $ff-text;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 19px;
    text-decoration: underline;
    transition: all 0.3s;

    &:hover {
      text-decoration: none;
    }
  }
}

.vacancy-form {
  &__item {
    margin-bottom: 2rem;
    font-family: $ff-text;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 19px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    &--multiple {
      align-items: flex-start;
      label {
        padding-top: 1.5rem;
      }
    }

    .select2-container input{
      min-width: 15rem;
    }

    &--small {
      .select2-container {
        min-width: 14.4rem;
        margin-left: 2rem;
      }
    }

    input {
      &::-webkit-input-placeholder {
        opacity: 1;
        letter-spacing: 1px;
      }
      &::-moz-placeholder {
        opacity: 1;
        letter-spacing: 1px;
      } /* Firefox 19+ */
      &:-moz-placeholder {
        opacity: 1;
        letter-spacing: 1px;
      } /* Firefox 18- */
      &:-ms-input-placeholder {
        opacity: 1;
        letter-spacing: 1px;
      }
    }

    label {
      min-width: 20%;
      color: #4a4a4a;
    }

    input[type="text"] {
      height: 4.5rem;
      padding: 1.6rem 2.5rem;
      color: #4a4a4a;
      letter-spacing: 1px;
    }
  }
}
