.position {
  padding: 3rem 5rem 0;
  color: $text-color;
  font-family: $ff-text;

  &__title {
    padding-bottom: 3rem;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);

    h3 {
      font-weight: 300;
      line-height: 2.8rem;
      text-transform: uppercase;
    }
  }

  &__subtitle {
    margin-bottom: 1.4rem;
  }

  &__box {
    padding: 3rem 0 5rem;

    .select2-container {
      margin-bottom: 3rem;
      min-width: 33.4rem;
    }
  }

  &__specialities {
    display: none;
    flex-wrap: wrap;
    padding-top: 2rem;
    padding-bottom: 3rem;

    &-item {
      width: 33%;

      .main-checkbox {
        margin-right: 1.5rem;
      }
    }

    &.active {
      display: flex;
    }
  }

  &__rank {
    padding-top: 2.4rem;
    max-width: 487px;

    &-default,
    &-item {
      padding: 2.3rem 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-default {
      font-weight: bold;
      letter-spacing: 1px;
      border-top: 1px solid rgba(151, 151, 151, 0.2);
      border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    }
    &-item {
      border-bottom: 1px solid rgba(151, 151, 151, 0.2);
      background-image: url("../img/drag_handle.svg");
      background-position: left center;
      background-size: 2rem;
      background-repeat: no-repeat;
    }

    &-title {
      margin-left: 4rem;
    }

    .select2-container {
      margin-bottom: 0;
      min-width: 15rem;
      .select2-selection--single {
        border: 0;
        box-shadow: none;
        background-color: transparent;
        font-family: $ff-text;
      }
    }
  }

  &__status {
    &.flex {
      display: flex;
      
      .position__status-item {
        margin-right: 20px;
      }

    }
  }
}

.skills {
  padding-top: 3.4rem;
  display: flex;
  justify-content: space-between;

  &__user {
    min-width: 50%;
  }
  &__user-list {
    display: flex;
    flex-direction: column;
  }
  &__user-item {
    width: 100%;
    // padding: 2.2rem 0;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    opacity: 0.2;
    display: flex;
    align-items: center;
    &:first-child {
      border-top: 1px solid rgba(151, 151, 151, 0.2);
    }

    .close-skill {
      display: none;
      padding: 0 10px;
    }

    &.active {
      opacity: 1;
    }

    // &.filled {
    //   .close-skill {
    //     display: block;
    //   }
    // }

    input[type="text"] {
      border: none;
      box-shadow: none;

      &:focus {
        box-shadow: none;
      }
      &::-webkit-input-placeholder {
        opacity: 1;
      }
      &::-moz-placeholder {
        opacity: 1;
      }
      &:-moz-placeholder {
        opacity: 1;
      }
      &:-ms-input-placeholder {
        opacity: 1;
      }
    }
  }

  &__rank {
    margin-right: 2rem;
  }

  &__offer {
    max-width: 22.5rem;
    padding-left: 1rem;
    margin-left: 5rem;
  }
  &__offer-item {
    color: #fa4a28;
    font-family: $ff-text;
    letter-spacing: 1px;
    line-height: 3.5rem;
  }
}

.invite {
  padding: 9rem 5rem 5rem;
  color: #4a4a4a;
  font-family: $ff-text;
  font-size: 1.6rem;
  letter-spacing: 1px;
  line-height: 1.9rem;

  &__title {
    text-align: center;

    h2 {
      margin-bottom: 1rem;
    }
  }

  &__emails {
    padding: 9rem 0 3.6rem;
  }

  &__email {
    margin-bottom: 2rem;
  }

  &__total {
    margin-left: 6.5rem;
    font-weight: bold;
  }
}

.skip-link {
  font-family: $ff-text;
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 3rem;
}

.salary {
  padding: 9rem 5rem 5rem;
  font-family: $ff-text;

  &__title {
    text-align: center;
    margin-bottom: 3rem;
    h2 {
      font-weight: normal;
    }
  }
}

.wishlist {
  margin-top: 4.3rem;
  &__list {
    margin-top: 4.8rem;
  }

  &__item {
    margin-bottom: 3rem;
    display: flex;

    label {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
    }
  }

  &__number {
    flex-shrink: 0;
    margin-right: 9rem;
    display: inline-block;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    color: #ffffff;
    font-size: 1.2rem;
    font-weight: bold;
    letter-spacing: 0.25rem;
    line-height: 3rem;
    text-align: center;
    background-color: $button-bg;

    position: relative;

    &::after {
      content: "";
      display: block;
      width: 2rem;
      height: 2.1rem;
      background-image: url("../img/drag_handle.svg");
      background-position: center center;
      background-size: 2rem;
      background-repeat: no-repeat;

      position: absolute;
      right: -4rem;
      top: 50%;
      transform: translate(0, -50%);
    }
  }

  .select2-container--default .select2-search--inline .select2-search__field {
    min-width: 180px;
    text-align: center;
  }

  .select2-container--default.select2-container--focus .select2-selection--multiple,
  .select2-container--default .select2-selection--multiple {
    border: 0;
    border-radius: 0;
    border-bottom: 1px solid #FA4A28;
  }

  .select2-container .select2-selection--multiple {
    min-height: auto;

    ::-webkit-input-placeholder {
      color: #FA4A28;
    }
    ::-moz-placeholder {
      color: #FA4A28;
    } /* Firefox 19+ */
    :-moz-placeholder {
      color: #FA4A28;
    } /* Firefox 18- */
    :-ms-input-placeholder {
      color: #FA4A28;
    }
  }

  .select2-container .select2-search--inline .select2-search__field {
    margin-top: 0;
  }

  .select2-container--default .select2-selection--multiple .select2-selection__choice {
    border: 0;
    background-color: white;
    margin-top: 0;
    color: #FA4A28;	
    font-family: "Fira Sans Condensed";	
    font-size: 14px;	
    letter-spacing: 0.88px;	
    line-height: 17px;
  }
  .select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    opacity: 0;
  }

}

.choose-salary {
  padding: 3.4rem 0 3.8rem;

  &__graph {
    display: flex;
    justify-content: center;
    margin-top: 7rem;
  }
}

.information {
  &__photo {
    margin-bottom: 5rem;
    display: flex;

    &-icon {
      width: 13.8rem;
    }

    &-text {
      max-width: 36.5rem;

      p:not(:last-child) {
        margin-bottom: 2.4rem;
      }
    }
  }

  &__link-item {
    input {
      margin-bottom: 1.8rem;
    }
    p {
      margin-bottom: 4.6rem;
    }
  }

  &__form {
    .information__contacts {
      margin-bottom: 2rem;
      display: flex;
      justify-content: space-between;
      input {
        width: 510px;
        max-width: 80%;
      }
    }

    .select-input {
      width: 510px;
      max-width: 80%;
      input {
        max-width: 100%;
      }
    }
  }
}

.resume {
  padding: 6rem 5rem;

  &-btn {
    padding-bottom: 5rem;
  }

  .btn {
    text-transform: uppercase;
    font-family: $ff-text;
    font-size: 1.8rem;
  }

  p {
    margin: 2rem 0;
  }

  &__item {
    margin-bottom: 2rem;
  }

  &__columns {
    display: flex;

    .select2-container {
      min-width: 25rem;
      margin: 0 3rem 0 0;
    }
  }

  &__column {
    &--allwidth {
      flex-grow: 1;
      margin-right: 2rem;
    }
  }
}
