.contacts {
  position: absolute;
  right: 0;

  &--employer {
    .user-search__button {
      color: #0373bb;
    }

    .contact__notification {
      background: radial-gradient(circle, #0373bb 0%, #54468f 100%);
    }
  }
}
.user-search {
  border: 1px solid rgba(84, 70, 143, 0.1);
  background-color: #efefef;
  max-width: 24.2rem;

  display: flex;

  .user-search__input {
    width: 100%;
    padding: 0.7rem 1rem;
    background-color: #efefef;
    border-radius: 0;
    border: 0;
    height: auto;
  }

  &__button {
    color: #54468f;
    font-size: 2rem;
    padding: 0.5rem 0.5rem 0.5rem 1.3rem;
    background-color: #efefef;
  }
}
