.cards {
  background-color: $bg-light;
  padding-bottom: 12rem;

  &__container {
    max-width: 1440px;
    margin: 0 auto;
    display: flex;
    justify-content: center;

    @include md-max {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    width: 39%;
    margin: 0 2.4rem;
    padding: 5.5rem 5em 6.2rem;
    background-color: $white;
    box-shadow: 0 2px 20px 15px rgba(0, 0, 0, 0.03);

    display: flex;
    flex-direction: column;

    @include md-max {
      width: 90%;
      max-width: 65rem;
    }

    p {
      margin: 3.3rem 0;
      text-align: center;
      font-family: $ff-text;
      font-size: 3rem;
      line-height: 3.9rem;
    }

    h1 {
      margin: 0;
      text-align: center;
    }

    .btn {
      min-width: 100%;
      margin-top: 0;
      padding: 1.9rem;
      border-radius: 3.75rem;
    }

    .link {
      text-align: center;
    }
  }

  &__item:not(:last-child){
    @include md-max {
      margin-bottom: 10rem;
    }

    @include sm-max {
      margin-bottom: 5rem;
    }
  }

  .sub-title {
    margin-bottom: 3rem;
    text-align: center;
  }
}

.logos {
  &__list {
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__item {
    margin-top: 5rem;
    margin-bottom: 5rem;
    width: 50%;
    text-align: center;

    img {
      width: 85%;
    }
  }
}
