.general-statistic {
  margin-bottom: 26px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  &__box {
    // min-width: 220px;
    width: 23%;
    max-width: 220px;
    margin-right: 15px;
    padding: 15px 36px 13px 36px;
    border-radius: 15px 0 15px 0;
    background-color: #ffffff;
    box-shadow: 0 2px 10px 5px rgba(84, 70, 143, 0.1);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  &__title {
    margin-bottom: 16px;
    opacity: 0.5;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 25px;
    text-transform: uppercase;
  }

  &__count {
    margin-bottom: 16px;
    font-family: $ff-talent;
    font-size: 46px;
    line-height: 48px;
    font-weight: 500;
    color: #54468f;

    &--small {
      margin-bottom: 23px;
      font-size: 35px;
      letter-spacing: 2.19px;
      line-height: 41px;
    }
  }

  &__percent {
    padding-left: 24px;
    color: #b82532;
    font-family: $ff-talent;
    font-size: 14px;
    font-weight: 900;
    letter-spacing: 0.88px;
    line-height: 16px;
    position: relative;

    ::before {
      content: '\f0dd';
      font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands";
      font-weight: 900;
      display: block;
      position: absolute;
      top: -25%;
      left: 0;
    }

    &--up {
      color: #7ED321;

      ::before {
        transform: rotate(180deg);
        top: 0;
      }
    }
  }
}