.profile-tabs {
  max-width: 60%;
}

#add-post {
  display: flex;

  textarea {
    flex-shrink: 1;
    background: none;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 16px;
    resize: none;
  }
}
#tabs-min {
  background: transparent;
  border: none;
  padding: 0;

  & .ui-widget-header {
    border: 1px solid rgba(84, 70, 143, 0.1);
    background-color: #e2e1e6;
    border-radius: 0px;
  }

  & .ui-tabs-nav .ui-state-default {
    background: transparent;
    border: none;
    border-right: 1px solid #ebeaef;
  }

  & .ui-tabs-nav .ui-state-active {
    background: transparent url(http://keith-wood.name/img/uiTabsArrow.png)
      no-repeat bottom center;
  }

  & .ui-tabs-nav .ui-state-default a {
    color: $tcolor;
    font-family: $ff-talent;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0.88px;
    line-height: 16px;
    text-transform: uppercase;
  }

  & .ui-tabs-nav .ui-state-active a {
    color: $tcolor;
  }

  .ui-tabs-panel {
    min-height: 14rem;
    padding: 1.5rem;
    background-color: #efefef;
    border-right: 1px solid rgba(84, 70, 143, 0.1);
    border-left: 1px solid rgba(84, 70, 143, 0.1);
    border-bottom: 1px solid rgba(84, 70, 143, 0.1);
  }
}

.add-content__list.ui-tabs-nav {
  padding: 0;
  margin: 0;
}

li.add-content__item.ui-tabs-tab {
  margin: 0;
  padding: 0;
}
