.user-employer {
  color: white;
  width: 80%;
  display: flex;
  justify-content: space-between;

  position: relative;

  &__icon {
    height: 15rem;
    width: 15rem;
    border-radius: 25px 0 25px 0;
    background-color: #ffffff;
    box-shadow: 3px 3px 15px 0 rgba(0, 0, 0, 0.25), -3px -3px 15px 0 rgba(255, 255, 255, 0.62);

    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 50%;
    left: -15rem;
    transform: translate(0, -50%);

    img {
      max-width: 13.3rem;
      max-height: 10rem;
    }
  }

  &__box {
    width: 100%;
    padding: 2.2rem 2.2rem 1rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__name {
    font-family: $ff-talent;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 1.5px;
    line-height: 28px;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 5px;
  }

  &__location {
    font-family: $ff-talent;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0.88px;
    line-height: 16px;
    text-transform: uppercase;
    margin-right: 12px;
  }

  &__like {
    display: flex;
    align-items: center;
    transition: all .3s;

    p {
      letter-spacing: 1px;
      line-height: 19px;
    }

    .fa-heart {
      font-size: 30px;
      cursor: pointer;
    }

    &.active {
      color: #b82532;
    }
  }
}
