.select-action {
  padding: 5px;
  background-color: #54468f;
  color: $white;

  display: flex;
  align-items: center;

  position: relative;

  &__button {
    padding-left: 10px;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    cursor: pointer;

    display: flex;
    justify-content: flex-end;
    align-items: center;

    &.active {
      i {
        top: 3px;      
        transform: rotate(180deg);
      }
    }

    p {
      margin-right: 12px;
      font-family: $ff-talent;
      font-size: 10px;
      font-weight: bold;
      line-height: 11px;
      text-transform: uppercase;
    }

    i {
      position: relative;
      top: -3px;
      transition: all .3s ease;
    }
  }

  &__indicator {
    flex-shrink: 0;
    margin-right: 5px;
    display: inline-block;
    height: 11px;
    width: 11px;
    border-radius: 50%;
    border: 1px solid #ffffff;
    background-color: #b82532;
  }

  &__dropdown {
    display: none;
    padding: 30px 20px;
    position: absolute;
    top: calc(100% + 3px);
    right: 0;
    min-width: 170px;
    background-color: #ffffff;
    box-shadow: 0 7px 10px 0 rgba(84, 70, 143, 0.29);
    z-index: 20;
  }

  &__item {
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;

    position: relative;
    &:not(:last-child) {
      margin-bottom: 15px;
    }

    &:after {
      content: "";
      position: absolute;
      display: block;
      top: 100%;
      right: 0;
      width: 0;
      height: 3px;
      background-color: #54468f;
      transition: all 0.3s;
    }

    &:hover::after {
      width: 67px;
    }

    &:hover .select-action__link {
      color: #54468F;	
      font-weight: bold;
  } 
  }

  &__link {
    color: #4a4a4a;
    font-family: $ff-talent;
    transition: all .3s;
  }
}
