.hero {
  padding-top: 17rem;
  background-color: $bg-light;
  background-position: right bottom;
  background-repeat: no-repeat;
  background-size: 45%;
  transition: all 0.7s ease;

  display: flex;
  align-items: flex-end;

  @include md-max {
    background-size: 80%;
  }

  @include sm-max {
    padding-top: 30rem;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    max-width: 175.2rem;
    width: 100%;
    margin: 0 auto;
    padding: 0 2rem;

    @include md-max {
      flex-direction: column;
    }
  }

  &__content {
    max-width: 70rem;
    margin-bottom: 9.3rem;
    margin-right: 5rem;
    margin-left: 13.6rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    font-size: 2.4rem;
    line-height: 3.2rem;
    font-weight: 500;
    color: $text-color;

    position: relative;

    @include xl-max {
      margin-left: 7rem;
    }

    @include md-max {
      margin-top: 6rem;
      align-self: center;
      display: flex;
      flex-direction: column;
    }

    @include sm-max {
      // margin-top: 35rem;
      margin-top: 2rem;
      margin-right: 7rem;
    }

    h1 {
      margin-top: 0;
      margin-bottom: 6.6rem;
    }

    p {
      max-width: 51.7rem;
    }

    .btn {
      @include md-max {
        align-self: center;
      }
    }

    .link {
      min-width: 40.9rem;
      text-align: center;

      @include md-max {
        align-self: center;
      }
    }
  }

  &__decor-element {
    position: absolute;
    top: 2.2rem;
    left: -14.3rem;

    transition: all .3s ease;

    @include xxl-max {
      left: -5rem;
    }

    @include lg-max {
      top: 1rem;
    }

    @include sm-max {
      display: none;
    }
  }

  &__img {
    margin-right: 5rem;
    max-width: 40%;
    @include md-max {
      max-width: 65%;
    }
  }

  &__title {
    margin-bottom: 6.5rem;
    color: $text-color;

    @include lg-max {
      min-width: 150%;
    }

    @include md-max {
      min-width: 100%;
    }
  }
}
