.employer,
.talent {
  color: #4a4a4a;
  // background-color: #f2f2f2;
  padding: 0;
  min-height: 100vh;
  transition: all 0.3s;
  // border-radius: 3.5rem 0 3.5rem 0;
  // box-shadow: 0 0 55px 15px rgba(0, 0, 0, 0.1);

  .user-profile {
    padding-top: 8.2rem;
  }

  h1 {
    font-family: $ff-talent;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 1.5px;
    line-height: 28px;
  }

  h2,
  h3,
  h4,
  h5,
  p {
    font-family: $ff-talent;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 16px;
  }

  form input {
    font-family: $ff-talent;
    font-size: 1.4rem;
    letter-spacing: 0.88px;
    line-height: 1.6rem;
    background-color: transparent;
  }
}

.talent {
  background-color: #e9e8ec;
}
.employer {
  background-color: #f6f5f9;
}
