.form__buttons {
  display: flex;
  justify-content: space-between;
  padding: 3rem 4.8rem 4.8rem;
  border-top: 1px solid rgba(151, 151, 151, 0.2);

  @include sm-max {
    padding: 3rem 2rem 4.8rem;
    .btn {
      min-width: 14rem;
    }
  }

  .btn {
    text-transform: uppercase;
  }
}
.form__button {
  display: flex;
  justify-content: flex-end;
  padding: 3rem 4.8rem 4.8rem;
  border-top: 1px solid rgba(151, 151, 151, 0.2);
}

.roles-form {
  min-height: 55rem;
  padding: 10rem 4.7rem;

  @include md-max {
  
  }

  &__list {
    display: flex;

    @include sm-max {
      flex-direction: column;
    }
  }

  &__items {
    flex-grow: 1;
  }

  &__item {
    margin-bottom: 2.5rem;
  }
}

.markets-form {
  padding: 5rem 0 9rem;

  &__images {
    max-width: 100%;
    display: flex;
  }

  &__city {
    margin: 2.8rem 5rem 0;
    display: flex;
    justify-content: space-between;

    @include sm-max {
      flex-direction: column;
      max-width: 25rem;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__subtitle {
    width: 18.5rem;
    text-transform: uppercase;
    font-size: 2.4rem;
    letter-spacing: 0.67px;
    line-height: 2.8rem;
    padding-bottom: 0.8rem;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    margin-bottom: 2rem;
  }

  .main-checkbox {
    margin-left: 0;
  }
}

.recruiting-form {
  min-height: 55rem;
  padding: 10rem 4.7rem;
}

// VIDEO adaptive

.video-wrap {
  position: relative;
  padding-bottom: 56.25%; /*     16:9 ( 4:3   75%) */
  height: 0;
  overflow: hidden;
}
.video-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-width: 0;
  outline-width: 0;
}

.instruction {
  padding: 2rem 4.8rem 9rem;

  &__list {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;

    @include sm-max {
      flex-direction: column;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 2.5rem;

      @include sm-max {
        margin-right: 0;
      }
    }
    @include sm-max {
      margin-bottom: 2rem;
    }
  }

  &__number {
    display: block;
    color: #fa4a28;
    font-size: 1.8rem;
    font-weight: bold;
    letter-spacing: 1.13px;
    line-height: 2.6rem;
    margin-bottom: 2.2rem;

    @include sm-max {
      display: inline-block;
      margin-right: 1rem;
      margin-bottom: 0;
    }
  }

  &__subtitle {
    min-height: 9.6rem;
    font-size: 1.6rem;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 2.3rem;
    margin-bottom: 0.8rem;

    @include sm-max {
      min-height: auto;
    }
  }
}

.conclusion {
    padding: 4.8rem;
    min-height: 72.3rem;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;

    &__title {
        h2 {
        font-size: 3.6rem;	
        font-weight: 300;	
        letter-spacing: 1px;	
        line-height: 4.4rem;     
        margin-bottom: 7rem;
        }
    }

    &__text {
        font-size: 2.5rem;	
        font-weight: 300;	
        letter-spacing: 1.56px;	
        line-height: 3.5rem;

        p:not(:last-child) {
           margin-bottom: 3.5rem; 
        }
    }


}
