.job-page {
  padding-top: 10rem;

  .job-about {
    width: auto;
  }
}

.modal {
  position: absolute;
  z-index: 10000;
  top: 0;
  left: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
}

.modal.open {
  visibility: visible;
}

.modal-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.3s, opacity 0.3s;
}

.modal-transition {
  transition: all 0.3s 0.12s;
  transform: translateX(90%);
  opacity: 0;
}

.modal.open .modal-transition {
  transform: translateX(0);
  opacity: 1;
}

.modal.open .modal-overlay {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.job {
  width: 63%;
  padding: 1.1rem 0 1.8rem 3rem;
  background-color: #efefef;
  height: 100vh;
  overflow-y: auto;
  position: absolute;
  right: 0;
  z-index: 99999;
  top: 0;

  &__content {
    display: flex;
    min-height: calc(100vh - 20rem);
  }

  &__line {
    height: 1px;
    width: 100%;
    background-color: rgba(84, 70, 143, 0.1);
  }

  p {
    letter-spacing: 1px;
    line-height: 21px;
  }

  &__subtitle {
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 19px;
    text-transform: uppercase;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }

    span {
      color: #b82532;
    }
  }
}

.new-window {
  margin-bottom: 2rem;
  padding: 5.5rem 0;
  border: 1px solid rgba(84, 70, 143, 0.1);
  background-color: #efefef;
  box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.05);

  display: flex;
  justify-content: flex-end;

  position: relative;

  .close-popup {
    position: absolute;
    left: -15px;
    top: 50%;
    transform: translate(0, -50%);

    &__icon {
      width: 30px;
      height: 30px;
      font-size: 20px;
      border-radius: 50%;
      border: 3px solid #54468f;
      color: #54468f;
      background-color: $white;

      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__link {
    width: 50%;
    padding-left: 2rem;
    font-family: $ff-talent;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 19px;
    color: #54468f;
    display: flex;

    &:hover {
      color: $accent;
    }
  }
}

.job-about {
  width: 50%;
  background-color: $white;
  box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.05);

  &__top {
    padding: 2rem;
    min-height: 10rem;
    border: 1px solid rgba(84, 70, 143, 0.1);
    background-color: #efefef;

    display: flex;
    align-items: center;
  }

  &__title {
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    font-weight: 900;
    letter-spacing: 1px;
    line-height: 19px;
    text-transform: uppercase;
  }

  &__description {
    padding: 0 2rem;
    background-color: $white;
  }

  &__box {
    padding: 2rem 0;
    color: #4a4a4a;
    // border-bottom: 1px solid rgba(84, 70, 143, 0.1);
  }
}

.employer-about {
  min-width: 282px;
  margin-left: 2rem;
  padding: 2.4rem 1rem 4rem 2rem;
  border: 1px solid rgba(84, 70, 143, 0.1);
  background-color: #ffffff;
  box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.05);
  color: #4A4A4A;

  display: flex;
  flex-direction: column;
  &__button {
    margin-bottom: 1rem;
  }

  &__inappropriate {
    display: inline-block;
    margin: 1rem 0 2rem;
    color: #b82532;
    font-family: $ff-talent;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 19px;
  }

  &__inappropriate input:checked ~ span,
  &__inappropriate input:checked ~ i {
    color: #54468f;
  }

  .employer-address {
    p,
    a {
      font-family: $ff-talent;
      font-size: 16px;
      letter-spacing: 1px;
      line-height: 19px;
    }

    a {
      text-decoration: underline;
      color: #54468f;

      &:hover {
        text-decoration: none;
      }
    }
  }
  .employer-location {
    h5 {
      font-size: 16px;
      margin-bottom: 1rem;
    }
  }

  .employer-statistic {
    flex-grow: 1;
    padding: 2.8rem 0;
    &__item {
      font-family: $ff-talent;
      font-size: 14px;
      font-weight: 500;
      letter-spacing: 0.88px;
      line-height: 16px;
      text-transform: uppercase;
      &:not(:last-child) {
        margin-bottom: 2.8rem;
      }

      a {
        text-decoration: underline;
        color: #54468f;

        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}
