.review {
  padding: 5rem;

  &__item {
    position: relative;
  }

  &__header {
    height: 8rem;
    background-color: rgba(84, 70, 143, 0.5);
  }

  &__edit {
    position: absolute;
    top: 1.6rem;
    right: 0;
    color: rgba(84, 70, 143, 0.5);
    cursor: pointer;
  }

  &__title {
    display: flex;
    margin-bottom: 3.5rem;

    h3 {
      font-weight: bold;
      letter-spacing: 0.15rem;
      line-height: 3.4rem;
      margin-bottom: 2.1rem;
    }
  }

  &__subtitle {
    font-size: 2.4rem;
    font-weight: bold;
    letter-spacing: 0.67px;
    line-height: 3.4rem;
    text-transform: uppercase;
    margin-bottom: 4rem;
  }

  &__photo {
    width: 13.6rem;
    margin-right: 3rem;
  }

  &__speciality {
    &-title {
      font-size: 2.4rem;
      font-weight: 300;
      letter-spacing: 0.67px;
      line-height: 2.8rem;
      text-transform: uppercase;
      margin-bottom: 2.1rem;
    }
  }

  &__preference {
    margin-bottom: 3.9rem;
    opacity: 0.8;

    &-item {
      padding-left: 3rem;
      margin-bottom: 2rem;
      position: relative;
      &::before {
        content: "";
        display: inline-block;
        min-width: 6px;
        min-height: 6px;
        background-color: #54468f;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
      }
    }
  }

  &__search-status {
    padding-bottom: 5.6rem;
    padding-left: 5rem;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    position: relative;
    &::before {
      content: "\f017";
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
      display: block;
      font-size: 2rem;
      color: #54468f;
      position: absolute;
      top: 0;
      left: 0;
    }
    &__title {
      margin-bottom: 1.6rem;
    }
  }

  &__contacts {
    padding: 2.5rem 0 2.5rem 1rem;
    color: $accent;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);

    span {
      margin-right: 3rem;
    }
  }

  &__wishlist {
    padding: 3.4rem 0;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);

    ol {
      counter-reset: myCounter;
    }

    li {
      list-style: none;
    }

    li:before {
      counter-increment: myCounter;
      content: counter(myCounter);
      color: #4a4a4a;
      display: inline-block;
      font-weight: bold;
      text-align: center;
      margin-right: 1rem;
      line-height: 40px;
    }
  }

  &__location {
    padding: 4rem 0 3.6rem 0;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    position: relative;

    .review__edit {
      top: 5rem;
    }

    &__content {
      padding-left: 5rem;
      position: relative;
      &::before {
        content: "\f3c5";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        display: block;
        font-size: 2.5rem;
        color: #54468f;
        position: absolute;
        top: 0;
        left: 0;
      }
    }

    &__title {
      margin-bottom: 1.6rem;
    }
  }

  &__primary-area {
    padding: 4rem 0 3rem;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    position: relative;

    .review__edit {
      top: 5rem;
    }

    &__list {
      display: flex;
    }

    &__item {
      padding: 1rem 2rem;
      margin-right: 2rem;
      background-color: $button-bg;
      border-radius: 2rem;
      color: $white;
      font-size: 1.6rem;
      font-weight: bold;
      letter-spacing: 1px;
      line-height: 2.3rem;
    }
  }

  &__experience {
    padding: 3rem 0;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    &__total {
      display: flex;
      justify-content: space-between;
      margin-bottom: 6rem;
      margin-top: 3.6rem;
    }

    &__column {
      width: 40%;
    }

    &__years {
      padding: 0 2rem;
      width: 80%;
      background-color: $button-bg;
      border-radius: 2rem;
      color: $white;
      font-size: 1.6rem;
      font-weight: bold;
      letter-spacing: 1px;
      line-height: 2.3rem;

      &--long {
        width: 100%;
      }
    }

    &__list {
      margin-top: 2.4rem;
    }

    &__item {
      margin-bottom: 1rem;
      display: flex;
      justify-content: space-between;

      span {
        font-weight: bold;
        margin-right: 2rem;
      }

      .review__experience__years {
        background-color: rgba(84, 70, 143, 0.5);
      }
    }
  }

  &__work-exp {
    padding: 3rem 0;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
  

    &__title {
      display: flex;
      justify-content: space-between;
    }
    &__content {
      padding-left: 5rem;
      position: relative;
      &::before {
        content: "\f19d";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        display: block;
        font-size: 2.5rem;
        color: #54468f;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__subtitle {
      margin-bottom: 1rem;
    }

    &__text {
        margin-top: 3rem;
    }
  }

  &__expect {
      padding: 6rem 0 3rem;
      .review__subtitle {
          display: inline-block;
          padding-top: 3rem;
          padding-right: 2rem;
          border-top: 4px solid #FA4A28;;
      }
  }
}
