.feed {
  max-width: 60%;
}

.posts {
  border: 1px solid rgba(84, 70, 143, 0.1);
  background-color: #efefef;
  padding: 1.5rem 0.8rem;

  &__title {
    margin: 2rem 0;
    text-transform: uppercase;
  }
}

.post {
  margin-bottom: 2.2rem;
  &__header {
    display: flex;
    align-items: center;
    margin-bottom: 2.9rem;
    padding: 0 0.7rem;
  }

  &__avatar {
    width: 4.5rem;
    height: 4.5rem;
    border-radius: 1rem 0 1rem 0;
    background-color: $white;
    overflow: hidden;
    flex-shrink: 0;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.14);
    margin-right: 1.5rem;

    display: flex;
    justify-content: center;
    align-items: center;

    &--logo img{
      max-width: 3.5rem;
    }
  }

  &__user-name {
    text-transform: uppercase;
    margin-bottom: 5px;
  }

  &__date {
    font-weight: normal;
  }

  &__text {
    padding: 0 0.7rem 2.3em;
    border-bottom: 1px solid #e2e1e6;
  }

  &__footer {
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  &__icon {
    font-size: 2.5rem;
    width: 2.5rem;
    flex-shrink: 0;
    color: #54468f;
    margin-left: 1rem;
    cursor: pointer;

    &--like {
      opacity: 0.5;
    }

    &--blue {
      color: #0373BB;
    }
  }

  &__buttons {
    margin-top: 10px;
    text-align: right;
  }
}
