.recent-activity {
  background-color: $white;
  padding-top: 21px;
  border-top: 1px solid rgba(84, 70, 143, 0.2);

  &__top {
    padding-bottom: 17px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    color: #383838;
    font-family: $ff-talent;
    font-size: 16px;
    font-weight: bold;
    line-height: 19px;
    letter-spacing: normal;
  }

  &__global-link {
    color: #54468f;
    font-family: $ff-talent;
    font-size: 14px;
    font-weight: bold;
    line-height: 16px;
    transition: all 0.3s ease;

    &:hover {
      opacity: 0.5;
    }
  }

  &__block {
    padding: 0 8px;
    max-height: 260px;
    overflow-y: auto;

    /* Width */
    &::-webkit-scrollbar {
      width: 4px;
    }

    /* Thumb */
    &::-webkit-scrollbar-thumb {
      background: #54468f;
    }
  }

  &__item {
    padding-bottom: 24px;
    border-left: 1px solid rgba(84, 70, 143, 0.2);
    display: flex;
    align-items: flex-start;
    position: relative;

    &--purple {
      .recent-activity__indicator .point-indicator {
        background-color: rgba(84, 70, 143, 0.2);

        &:after {
          background-color: #54468f;
        }
      }

      .recent-activity__icon {
        background-color: #54468f;
      }

      .recent-activity__link {
        color: #54468f;
      }
    }

    &--blue {
      .recent-activity__indicator .point-indicator {
        background-color: rgba(3, 116, 187, 0.2);

        &:after {
          background-color: #0374bb;
        }
      }

      .recent-activity__icon {
        background-color: #0374bb;
      }

      .recent-activity__link {
        color: #0374bb;
      }
    }

    &--red {
      .recent-activity__indicator .point-indicator {
        background-color: rgba(184, 37, 50, 0.2);

        &:after {
          background-color: #b82532;
        }
      }

      .recent-activity__icon {
        background-color: #b82532;
      }

      .recent-activity__link {
        color: #b82532;
      }
    }

    &--green {
      .recent-activity__indicator .point-indicator {
        background-color: rgba(126, 211, 33, 0.2);

        &:after {
          background-color: #7ed321;
        }
      }

      .recent-activity__icon {
        background-color: #7ed321;
      }

      .recent-activity__link {
        color: #7ed321;
      }
    }
  }

  &__icon {
    margin-left: 8px;
    border-radius: 50%;
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;

    i {
      font-size: 14px;
      color: $white;
    }
  }

  &__content {
    margin-left: 20px;
    padding-right: 5px;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 14px;
    line-height: 18px;
  }

  &__link {
    text-decoration: underline;
    transition: all 0.3s;

    &:hover {
      text-decoration: none;
    }
  }

  &__time {
    opacity: 0.5;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 12px;
    line-height: 14px;
  }

  &__indicator {
    position: relative;
    top: 0;
    left: -8px;
    min-width: 16px;
    min-height: 16px;
    border-radius: 50%;
    background-color: $white;
    display: flex;
    justify-content: center;
    align-items: center;

    .point-indicator {
      display: block;
      width: 16px;
      height: 16px;
      border-radius: 50%;

      position: relative;

      &::after {
        content: "";
        display: block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}
