.header {
  padding: 1.4rem 2rem;
  width: 100%;
  position: absolute;

  &--empty {
    display: none;
  }

  &--signUp {
    position: relative;
    background-color: $white;

    @include sm-max {
      background-color: #EFEFEF;
    }

    .btn {
      background-color: $accent;
      border-color: $accent;
    }
  }

  &__container {
      max-width: 144rem;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;

      @include sm-max {
        flex-direction: column;
        align-items: center;
      }
  }

  &__logo {
      max-width: 13.3rem;

      @include sm-max {
        margin-bottom: 5rem;
      }
  }  
}