.jobs {
  padding-top: 8.1rem;

  .container {
    background-color: #e9e8ec;
    position: fixed;
    right: 0;
    left: 0;
    z-index: 998;
  }

  &__menu {
    margin: 1rem auto;
    padding: 0.8rem 1rem;
    border: 1px solid rgba(84, 70, 143, 0.1);
    background-color: #efefef;

    display: flex;
    align-items: center;
  }

  &__box {
    height: 4.6rem;
    padding: 0.8rem 0;
    border: 1px solid rgba(84, 70, 143, 0.1);
    border-radius: 5px;
    background-color: #efefef;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__nav {
    min-width: 152px;
    position: relative;

    .jobs__box-item:not(:last-child) {
      border: none;
    }

    &:hover {
      box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.05);
    }

    &.open {
      border-radius: 5px 5px 0 0;
      box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.05);

      & .nav-dropdown {
        display: block;
      }

      & .fa-bars {
        display: none;
      }

      & .fa-times {
        display: block;
      }
    }

  }

  .nav-dropdown {
    display: none;
    position: absolute;
    top: 100%;
    width: 100%;
    border: 1px solid rgba(84, 70, 143, 0.1);
    box-shadow: 0 0 15px 5px rgba(0,0,0,0.05);
    border-radius: 0 0 5px 5px;
    background-color: #efefef;
    padding: 1.5rem 0.7rem;
    min-width: 152px;

    &__item {
      font-family: Roboto;
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 0.88px;
      line-height: 16px;
      text-transform: uppercase;

      &:not(:last-child) {
        margin-bottom: 2rem;
      }

      &:last-child {
        opacity: 0.5;
      }
    }

    &__line {
      width: 119px;
      height: 1px;
      background-color: rgba(84, 70, 143, 0.1);
      margin: 4rem 0.8rem 1.3rem;
    }

    &__link {
      color: #4a4a4a;
      transition: all.3s;
      &:hover {
        color: #54468f;
      }
    }
  }

  &__box-item {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    padding: 0 2.1rem;

    &--small {
      padding: 0 0 0 0.8rem;
    }

    h3 {
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 1px;
      line-height: 19px;
    }
    &:not(:last-child) {
      border-right: 1px solid #e2e1e6;
    }

    .recent__icon {
      margin-left: 3.4rem;
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      font-size: 3rem;
      color: #54468f;
    }

    &--burger {
      font-size: 20px;
      color: #54468f;
      cursor: pointer;
    }

    .fa-times {
      display: none;
    }

    &--accent {
      color: #b82532;
    }

    .heart {
      font-size: 1.2rem;
      margin-left: 5px;
    }
  }

  &__switch {
    height: 4.6rem;
    margin: 0 auto;
    display: flex;
    border: 1px solid rgba(84, 70, 143, 0.1);
    border-radius: 5px;
    background-color: #efefef;

    &__item {
      padding: 0.2rem 0.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 12.5rem;
      text-transform: uppercase;
      text-align: center;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 1px;
      line-height: 19px;

      &.active {
        border: 1px solid rgba(84, 70, 143, 0.1);
        background-color: rgba(84, 70, 143, 0.1);
      }
    }

    &__link {
      color: $tcolor;

      &--accent {
        font-size: 1.9rem;
        color: $red;
      }
    }
  }
}
