.actions-modal {
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 30;

  .modal-overlay {
    visibility: visible;
    opacity: 0.5;
  }
}

.t-profile {
  position: relative;
  padding: 30px 30px 25px 24px;
  max-width: 844px;
  min-width: 844px;
  background-color: #ffffff;
  overflow-y: auto;
  height: 100%;
  z-index: 40;

  display: flex;

  .close-popup {
    position: absolute;
    top: 15px;
    right: 20px;

    i {
      font-size: 32px;
      opacity: 0.5;
      color: #54468f;
    }
  }

  &__icon {
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: solid 4px #54468f;
    background-color: #54468f;
    opacity: 0.5;
    overflow: hidden;

    display: flex;
    align-items: flex-end;
    justify-content: center;

    i {
      font-size: 50px;
      color: #ffffff;
    }
  }

  &__content {
    width: 100%;
    margin: 0 20px 0 30px;
  }

  &__title {
    display: flex;
    align-items: center;
  }

  &__name {
    margin-right: 10px;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 1.5px;
    line-height: 28px;
  }

  &__status {
    margin: 0 5px;
    padding: 4px 7px;
    background-color: #fa4a28;
    color: #ffffff;
    font-family: $ff-talent;
    font-size: 10px;
    font-weight: bold;
    letter-spacing: 0.63px;
    line-height: 11px;
    text-transform: uppercase;

    &--green {
      background-color: #7ed321;
    }
  }

  &__location {
    margin-bottom: 11px;

    p {
      color: #4a4a4a;
      font-family: $ff-talent;
      font-size: 16px;
      letter-spacing: 1px;
      line-height: 24px;
    }
  }

  &__contacts {
    margin-bottom: 19px;
    display: flex;
    align-items: center;

    &-item {
      margin-right: 29px;
      display: flex;
      align-items: center;
      opacity: 0.8;
      color: #54468f;

      p {
        font-family: $ff-talent;
        font-size: 16px;
        font-weight: bold;
        letter-spacing: 1px;
        line-height: 19px;
      }

      i {
        opacity: 0.8;
        font-size: 15px;
        margin-right: 10px;
      }
    }
  }

  &__text {
    p {
      margin-bottom: 10px;
      color: #4a4a4a;
      font-family: $ff-talent;
      font-size: 14px;
      letter-spacing: 0.88px;
      line-height: 16px;
    }
  }

  &__skills {
    padding: 13px 0 46px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  .skills__list {
    display: flex;
    align-items: center;
  }

  .skills__item {
    margin-right: 20px;
    padding: 6px 20px;
    min-width: 80px;
    background-color: #54468f;
    color: rgba(255, 255, 255, 0.8);
    font-family: "Fira Sans Condensed";
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    text-align: center;
    border-radius: 15px;
  }

  .select-action {
    min-height: 31px;	
    min-width: 120px;

    i {
      padding-left: 5px;
      padding-right: 10px;
      font-size: 24px;
    }
  }
}
