.dashboard__calendar {
  // min-width: 336px;
  // height: 346px;
  background-color: #fff;
  padding-bottom: 20px;

  #calendar {
    display: none;
  }

  .datepicker-inline .datepicker {
    border: 0;
    margin: 0 auto;
    max-width: 313px;
    width: auto;
  }

  .datepicker--nav {
    border: none;
    padding: 11px 4px 20px;
  }

  .datepicker--nav-title,
  .datepicker--nav-title i {
    color: #383838;
    font-family: $ff-talent;
    font-size: 12px;
    font-weight: bold;
    line-height: 8px;
  }

  .datepicker--nav-action {
    position: relative;

    svg {
      display: none;
    }

    &::before {
      content: "\f0dd";
      font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands";
      font-weight: 900;
      color: #54468f;
      transform: rotate(90deg);
    }
  }

  .datepicker--nav-action[data-action="next"] {
    transform: rotate(180deg);
  }

  .datepicker--days-names {
    margin-bottom: 7px;
    padding-bottom: 9px;
    border-bottom: 0.5px solid #dbdcdd;
  }

  .datepicker--day-name {
    color: #8c8f93;
    font-family: $ff-talent;
    font-size: 7px;
    font-weight: 500;
  }

  .datepicker--cell {
    margin-bottom: 5px;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 12px;
    line-height: 14px;
  }

  .datepicker--cell.-current- {
    color: #0374bb;
  }

  .datepicker--cell-day.-other-month-,
  .datepicker--cell-year.-other-decade- {
    opacity: 0;
  }

  .datepicker--cell.-selected-,
  .datepicker--cell.-selected-.-current-,
  .datepicker--cell.-selected-.-focus- {
    background-color: #54468f;
    color: #fff;
  }

  .datepicker--cell.-in-range- {
    background-color: rgba(84, 70, 143, 0.2);
  }

  // .datepicker--cell.-range-from-,
  // .datepicker--cell.-range-to- {
  //   border-radius: 100px;
  // }
}
