.dashboard {
  &__menu {
    padding: 30px 50px;
    display: flex;
    justify-content: flex-end;

    position: relative;

    &.active {
      .modal-overlay {
        transition: all 0.3s ease;
        visibility: visible;
        opacity: 1;
        z-index: 20;
      }
      .dashboard__burger-menu {
        visibility: visible;
        z-index: 20;
      }

      .dashboard__burger {
        color: #fff;
        z-index: 20;
      }
    }
  }

  &__burger {
    font-size: 32px;
    color: #54468f;
    transition: all 0.3s;
  }

  &__burger-menu {
    visibility: hidden;
    position: absolute;
    top: calc(100% - 30px);
    min-width: 250px;
    background-color: #fff;
    text-align: right;
    padding: 43px 30px;

    z-index: 20;
  }

  .burger-menu__item {
    position: relative;
    &:not(:last-child) {
      margin-bottom: 30px;
    }

    &:after {
        content: "";
      position: absolute;
      display: block;
      top: calc(100% + 5px);
      right: 0;
      width: 0;
      height: 3px;
      background-color: #54468f;
      transition: all .3s;
    }

    &:hover::after {
        width: 90px;
    } 

    &:hover .burger-menu__link {
        opacity: 1;
        color: #54468F;	
        font-weight: bold;
    } 
  }

  .burger-menu__link {
    opacity: 0.5;
    color: #4a4a4a;
    font-family: $ff-talent;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    transition: all .3s;
  }
}
